import React from 'react'
import './Travels.css'
import Koln from '../Koln'
import Berlin from '../Berlin'
import Valencia from '../Valencia';




async function getCityKoln() {
    const response = await fetch('https://retoolapi.dev/lpBnQB/cities?city=Koln');
    return response.json();
}
async function getCityBerlin() {
    const response = await fetch('https://retoolapi.dev/lpBnQB/cities?city=Berlin');
    return response.json();
}

async function getCityValencia() {
    const response = await fetch('https://retoolapi.dev/lpBnQB/cities?city=Valencia');
    return response.json();
}
const CityListLoading = () => (<div className="spin"></div>);
class Travels extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            cityKoln: [],
            cityBerlin: [],
            cityValencia: [],
            
            isLoading: false,

        }
    }
    componentDidMount() {
        // isLoading = true
        this.setState({ isLoading: true })
        getCityKoln()
            .then((koln) => {
                console.log(koln);
                this.setState({
                    cityKoln: koln,
                    isLoading: false
                })
                // isLoading = false
            })
            .catch((error) => {
                // isLoading = false
                this.setState({ isLoading: false })
            })

        getCityBerlin()
            .then((berlin) => {
                console.log(berlin);
                this.setState({
                    cityBerlin: berlin,
                    isLoading: false
                })
                // isLoading = false
            })
            .catch((error) => {
                // isLoading = false
                this.setState({ isLoading: false })
            })
        getCityValencia()
            .then((valencia) => {
                console.log(valencia);
                this.setState({
                    cityValencia: valencia,
                    isLoading: false
                })
                // isLoading = false
            })
            .catch((error) => {
                // isLoading = false
                this.setState({ isLoading: false })
            })
       


    }

    renderCityBerlin() {
        const { cityBerlin, isLoading } = this.state;
        if (isLoading) {
            return (<CityListLoading />);
        }
        return cityBerlin.map(cityItem => {
            return (<Berlin key={cityItem.id}
                id={cityItem.id}
                title={cityItem.city}
                image1={cityItem.src1}
                image2={cityItem.src2}
                title2={cityItem.cdescription}
                description={cityItem.mtext} />)
        })

    }
    renderCityValencia() {
        const { cityValencia, isLoading } = this.state;


        if (isLoading) {
            return (<CityListLoading />);
        }

        return cityValencia.map(cityItem => {
            return (<Valencia key={cityItem.id}
                id={cityItem.id}
                title={cityItem.city}
                image1={cityItem.src1}
                image2={cityItem.src2}
                title2={cityItem.cdescription}
                description={cityItem.mtext}

            />)
        });


    }
       
    renderCityKoln() {
        const { cityKoln, isLoading } = this.state;


        if (isLoading) {
            return (<CityListLoading />);
        }

        return cityKoln.map(cityItem => {
            return (<Koln key={cityItem.id}
                id={cityItem.id}
                title={cityItem.city}
                image1={cityItem.src1}
                image2={cityItem.src2}
                title2={cityItem.cdescription}
                description={cityItem.mtext}

            />)
        });

    }
    render() {
        return (
            <div className="container-fluid city-list">


                {this.renderCityKoln()}
                {this.renderCityBerlin()}
                {this.renderCityValencia()}
                
            </div>
        )
    }

}

export default Travels;