import React from 'react'
import Nav from '../../Components/Nav';
import constructions from '../../Pics/Development/maintainance.jpg'
function AboutMe(){
    return (
        <div>
            <Nav/>
            <div className="container-fluid">
                <img src={constructions} alt="Constructions" className="img-fluid" width="100%" />
            </div>
        </div>
    )
}

export default AboutMe;