import React from 'react'
import './Section2Home.css'
import shop from '../../Pics/shop ideea1.png'
import { Link } from 'react-router-dom';


function Section2Home(){
    return(
        <div className="container-fluid container-rotate">
            <div className="row">
                <div className="col-sm-6">
                    <img src={shop} alt="Shop idea" width="80%" className="rotated"/>
                </div>
                <div className="col-sm-6 straight">
                    <h1>You can check my apps on the Portofolio Page</h1>
                    <h3>I have to warn you that they are for begginers</h3>
                    <h3>I am Enthusiastic and fast learner but not an expert</h3>
                    <Link to="/portofolio" className="btn btn-info" role="button">Portofolio</Link>
                </div>
               
            </div>
        </div>
    )
}

export default Section2Home;