import React from 'react'
import './Berlin.css'

class Berlin extends React.Component{
    constructor(props){
        super(props)
        this.state={

        }

    }

    render(){
        return(
            <div className="city" key={this.props.id} >

                <div className="row">
                    <div className="col-sm-4">
                        <img src={this.props.image1} alt={this.props.City} width="80%" className="rotated" />
                    </div>
                    <div className="col-sm-4 straight">
                        <h1>{this.props.title}</h1>
                        <h2>{this.props.title2}</h2>

                    </div>
                    <div className="col-sm-4">
                        <img src={this.props.image2} alt={this.props.City} width="80%" className="rotated-right" />
                    </div>


                </div>
                
            </div>
        )
    }
}

export default Berlin