import React from 'react'

import KolnModal from '../KolnModal';
import './Koln.css'



async function getCityKoln() {
    const response = await fetch('https://retoolapi.dev/lpBnQB/cities?city=Koln');
    return response.json();
}

const CityListLoading = () => (<div className="spin"></div>);

class Koln extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            cityKoln:[],
            isLoading:false,
            show: false
        }
        this.showModal = this.showModal.bind(this);
        this.hideModal = this.hideModal.bind(this);
    }

    
        showModal = () => {
        this.setState({ show: true });
    };

      hideModal = () => {
        this.setState({ show: false });
    };
    
    componentDidMount(){
        this.setState({ isLoading: true })
        getCityKoln()
            .then((koln) => {
                console.log(koln);
                this.setState({
                    cityKoln: koln,
                    isLoading: false
                })
                // isLoading = false
            })
            .catch((error) => {
                // isLoading = false
                this.setState({ isLoading: false })
            })
        
    }
    


    renderKolnModal() {
        const{cityKoln,isLoading}=this.state
        if (isLoading) {
            return (<CityListLoading />);
        }

       return cityKoln.map(cityItem=>{
            return(<KolnModal key={cityItem.id}
            img1={cityItem.src3}
            img2={cityItem.src4}
            img3={cityItem.src5}
            title={cityItem.city}
            text={cityItem.mtext}
            show={this.state.show}
            handleClose={this.hideModal}
            />)
       })
    }


    render() {
        return (
            <div className="city" key={this.props.id} >

                <div className="row">
                    <div className="col-sm-4">
                        <img src={this.props.image1} alt={this.props.City} width="80%" className="rotated" />
                    </div>
                    <div className="col-sm-4 straight">
                        <h1>{this.props.title}</h1>
                        <h2>{this.props.title2}</h2>
                        <button onClick={this.showModal}>open{this.props.title}</button>
                        {this.renderKolnModal()}

                    </div>
                    <div className="col-sm-4">
                        <img src={this.props.image2} alt={this.props.City} width="80%" className="rotated-right" />
                    </div>


                </div>

            </div>
        )
    }
}





export default Koln;