import React from 'react'
import './HeaderHome.css'
import dev from '../../Pics/developer.jpg'


function HeaderHome() {
    return (
        <div className="container-fluid header-h">
            <div className="row">
                <div className="col-sm-6 rotate">
                    <img src={dev} alt="developer" className="rotated" width="80%"/>
                </div>
                <div className="col-sm-6 straight" >
                    <h1>My Name Is Alex</h1>
                    <h2>I am an Enthusiastic  Front-End Developer</h2>
                    <h3>When i have an App idea I just power up the laptop and start working</h3>
                </div>
            </div>
        </div>
    )
}
export default HeaderHome;