import React from 'react'
import './SectionHome.css'
import plane from '../../Pics/plane1.jpg'
import { Link } from 'react-router-dom';


function SectionHome(){
    return(
        <div className="container-fluid sec">
           <div className="row">
               <div className="col-sm-6 straight-left">
                   <h1>I like also to travel</h1>
                   <h2>On two of my previous Jobs traveling was a must </h2>
                   <Link to="/Hobbies" className="btn btn-info" role="button">Hobbies</Link>
               </div>
               <div className="col-sm-6 rotate">
                   <img src={plane} alt="Plane" className="rotated-right" width="70%"/>

               </div>
           </div>
        </div>
    )
}

export default SectionHome;