import React from 'react'
import Salzburg from '../Salzburg';
import Stockholm from '../Stockholm';
import Stuttgart from '../Stuttgart';
import './Travels2.css'



async function getCityStockholm() {
    const response = await fetch('https://retoolapi.dev/lpBnQB/cities?city=Stockholm');
    return response.json();
}
async function getCitySalzburg() {
    const response = await fetch('https://retoolapi.dev/lpBnQB/cities?city=Salzburg');
    return response.json();
}
async function getCityStuttgart() {
    const response = await fetch('https://retoolapi.dev/lpBnQB/cities?city=Stuttgart');
    return response.json();
}


const CityListLoading = () => (<div className="spin"></div>);

class Travels2 extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            cityStockholm: [],
            citySalzburg: [],
            cityStuttgart: [],
            isLoading: false,
        }
    }


    componentDidMount() {
        this.setState({ isLoading: true })
        getCitySalzburg()
            .then((sz) => {
                console.log(sz);
                this.setState({
                    citySalzburg: sz,
                    isLoading: false
                })
                // isLoading = false
            })
            .catch((error) => {
                // isLoading = false
                this.setState({ isLoading: false })
            })
        getCityStockholm()
        .then((st)=>{
            console.log(st);
            this.setState({
                cityStockholm:st,
                isLoading:false
            })
        })
        .catch((error) => {
            // isLoading = false
            this.setState({ isLoading: false })
        })
        getCityStuttgart()
        .then((stu)=>{
            console.log(stu);
            this.setState({
                cityStuttgart:stu,
                isLoading:false
            })
        })
        .catch((error) => {
            // isLoading = false
            this.setState({ isLoading: false })
        })




    }
    renderCitySalzburg() {
        const { citySalzburg, isLoading } = this.state;
        if (isLoading) {
            return (<CityListLoading />);
        }
        return citySalzburg.map(cityItem => {
            return (<Salzburg key={cityItem.id}
                id={cityItem.id}
                title={cityItem.city}
                image1={cityItem.src1}
                image2={cityItem.src2}
                title2={cityItem.cdescription}
                description={cityItem.mtext} />)
        })

    }
    renderCityStockholm() {
        const { cityStockholm, isLoading } = this.state;
        if (isLoading) {
            return (<CityListLoading />);
        }
        return cityStockholm.map(cityItem => {
            return (<Stockholm key={cityItem.id}
                id={cityItem.id}
                title={cityItem.city}
                image1={cityItem.src1}
                image2={cityItem.src2}
                title2={cityItem.cdescription}
                description={cityItem.mtext} />)
        })

    }
    renderCityStuttgart() {
        const { cityStuttgart, isLoading } = this.state;
        if (isLoading) {
            return (<CityListLoading />);
        }
        return cityStuttgart.map(cityItem => {
            return (<Stuttgart key={cityItem.id}
                id={cityItem.id}
                title={cityItem.city}
                image1={cityItem.src1}
                image2={cityItem.src2}
                title2={cityItem.cdescription}
                description={cityItem.mtext} />)
        })

    }

    render(){
        return(
            <div className="city2">
                {this.renderCitySalzburg()}
                {this.renderCityStockholm()}
                {this.renderCityStuttgart()}

            </div>
        )
    }
}

export default Travels2