import React from 'react'

import './KolnModal.css'

const KolnModal = ({ handleClose, show, img1, img2, img3, title, text }) => {
    const showHideClassName = show ? "modal display-block" : "modal display-none";

    return (
        <div className={showHideClassName}>
            <section className="modal-main">
                <h1>{title}</h1>
                <div className="container caro">
                <div id="myCarousel" className="carousel slide" data-ride="carousel">

                <ol className="carousel-indicators">
                <li data-target="#myCarousel" data-slide-to="0" className="active"></li>
                <li data-target="#myCarousel" data-slide-to="1"></li>
                <li data-target="#myCarousel" data-slide-to="2"></li>
                </ol>


                <div className="carousel-inner">
                <div className="item active">
                <img src={img1} alt={title} width="100%" />
                </div>

                <div className="item">
                <img src={img2} alt={title} width="100%" />
                </div>

                <div className="item">
                <img src={img3} alt={title} width="100%" />
                </div>
                </div>


                <a className="left carousel-control" href="#myCarousel" data-slide="prev">
                <span className="glyphicon glyphicon-chevron-left"></span>
                <span className="sr-only">Previous</span>
                </a>
                <a className="right carousel-control" href="#myCarousel" data-slide="next">
                <span className="glyphicon glyphicon-chevron-right"></span>
                <span className="sr-only">Next</span>
                </a>
                </div>
                <div className="content">
                <p>{text}</p>
                </div>
                <button type ="button" className="btn ntn-info" onClick={handleClose}>
                Close
                </button>
                </div>
            </section>
        
        </div >
    );
};


export default KolnModal;